.item {
    display: flex;
    flex-direction: row;
    margin-top: 5px;
    justify-content: space-between;
    color: #464646;
}

.itemArea {
    display: flex;
    align-items: flex-start;
}

.avatar {
    width: 60px;
    height: 60px;
    border-radius: 100%;
    margin-right: 10px;
}

.text {
    max-width: 75%;
}

.like {
    display: flex;
    align-items: center;
    color: rgba(70, 70, 70, 0.5);
}

.likeIcon:hover, .active {
    color: rgb(255, 145, 145);
}

.deleteIcon:hover {
    color: #1c3faa;
}

.likeIcon, .deleteIcon {
    margin: 0 5px;
    transform: scale(1.5);
}